import wPicker from "@/components/w-picker/w-picker.vue";
import pictureTailor from "@/components/picture-tailor/pictureTailor.vue";
export default {
	name: 'personaldata',
	components: {
		wPicker,
		pictureTailor
	},
	data() {
		return {
			avatarTempPath: '',
			avatarCropPath: '',
			selectList: [{
				label: "男",
				value: 0
			}, {
				label: "女",
				value: 1
			}],
			mode: 'date',
			defaultVal: [0, 0, 0, 0, 0, 0, 0],
			sex: '女',
			date: '选择生日', //生日
			region: '选择地区'
		}
	},
	methods: {
		chooseImage() {
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'],
				sourceType: ['album'],
				success: (res) => {
					this.avatarTempPath = res.tempFilePaths.shift()
				}
			})
		},
		cropComplete(val) {
			// console.log(val);
			this.avatarTempPath = '';
			this.avatarCropPath = val;
		},
		cropCancel() {
			this.avatarTempPath = '';
		},
		togglePicker(mode) {
			this.mode = mode;
			this.$refs[mode].show();
		},
		onConfirm(val) {
			// console.log(val);
			//如果页面需要调用多个mode类型，可以根据mode处理结果渲染到哪里;
			switch (this.mode) {
				case "date":
					this.date = val.result;
					break;
				case "selector":
					this.sex = val.result;
					break;
				case "region":
					this.region = val.result;
					break;
			}
		},
		submitInfo(e) {
			// console.log(e);
			uni.navigateTo({
				url: '../home/index',
				animationDuration: 300,
				animationType: 'fade-in'
			})
		}
	}
}
